body {
  font-family: "Montserrat", sans-serif;
}

button,
input {
  font-family: "Montserrat", sans-serif;
}

* {
  flex-shrink: 0;
}

.container {
  max-width: 1140px;
  width: 95%;
  margin: 0 auto;
  padding: 1rem 0;
}

.searchBar-wrap {
  display: flex;
  flex-direction: row;
  background-color: #f0f0f0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 2.5rem auto 4rem auto;
  padding: 0.5rem;
  border-radius: 5px;
}

.searchBar-wrap form {
  background-color: #f0f0f0;
  outline: none;
  border: none;
}

.searchBar-wrap form input {
  margin: 0 0.5rem 0 0;
  padding: 0.3rem;
  border-radius: 8px;
}

.searchBar-wrap svg {
  padding-right: 0.5rem;
  cursor: pointer;
  height: 2rem;
  padding: 0.2rem;
}

.searchBar-wrap button {
  outline: none;
  border: none;
  padding: 0.3rem 1rem;
  border-radius: 5px;
  background-color: #0f52ba;
  color: #fff;
}

.blogList-wrap {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 3rem;
}

@media (max-width: 768px) {
  .blogList-wrap {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 600px) {
  .blogList-wrap {
    grid-template-columns: 1fr;
  }
}

.chip {
  font-size: 0.7rem;
  background: linear-gradient(to right, #106cfc, #5e1d09);
  color: #fff;
  padding: 0.2rem 0.2rem;
  border-radius: 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-transform: capitalize;
}

.blogItem-wrap {
  display: flex;
  background: #fff;
  flex-direction: column;
  text-overflow: ellipsis;
}

.container {
  background-color: white;
}

.blogItem-cover {
  width: 100%;
  height: 250px;
  object-fit: cover;
  margin-bottom: 0.5rem;
  border-radius: 20px;
}

.blogItem-wrap h3 {
  margin: 0.5rem 0 0.5rem 0;
  flex: 1;
}

.blogItem-wrap p {
  position: relative;
  max-height: 50px;
  overflow: hidden;
  font-size: 0.8rem !important;
  padding-right: 0.6rem;
  color: #a9a9a9;
}

/* .blogItem-desc::before {
    position: absolute;
    content:'...';
    bottom:0; right:0;
} */

.blogItem-wrap footer {
  display: flex;
  align-items: center;
  margin-top: 1rem;
  justify-content: space-between;
}

.blogItem-link {
  text-decoration: none;
  color: inherit;
}

.blogItem-author {
  display: flex;
  align-items: center;
}

.blogItem-author img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 0.5rem;
}

.blogItem-author p {
  font-size: 0.6rem;
  color: #a9a9a9;
  font-weight: 600;
}

.emptyList-wrap {
  display: flex;
  justify-content: center;
}

.emptyList-wrap img {
  max-width: 250px;
  width: 100%;
}

.blog-wrap {
  max-width: 700px;
  margin: 0 auto;
}

.blog-goBack {
  text-decoration: none;
  font-size: 0.8rem;
  color: #a9a9a9;
  font-weight: 500;
  margin-bottom: 2rem;
  display: block;
}

.blog-wrap header {
  text-align: center;
}

.blog-date {
  font-size: 0.8rem;
  color: #a9a9a9;
  font-weight: 500;
}

.blog-wrap img {
  width: 100%;
}

.blog-subCategory {
  display: flex;
  justify-content: center;
}

.blog-subCategory > div {
  margin: 1rem;
}

.blog-desc {
  padding: 1rem;
  margin-top: 1.5rem;
}

.cover-image img {
  max-height: 400px;
  background: url("http://localhost:8080/loading.gif") 50% no-repeat;
}

.cover-image p {
  color: #a9a9a9;
  font-size: 0.8rem;
}

.text-center .text-center-point {
  text-align: justify;
  list-style-type: disc;
}

